h4{
    margin: 5px;
    width: 170px;
    height: 190px;
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 10px;
    box-shadow: 1px 2px 10px gray;
    cursor: pointer;
}

.button{
    position: relative;
    /* bottom: 120px; */
    justify-content: center;
    display: flex;
    flex-direction: row;
    margin-top: 80px;
}

.btn{
    padding: 45px;
    border: none;
    border-radius: 150px;
    position: relative;
    cursor: pointer;
    height: 140px;
    margin: 5px;
    box-shadow: 1px 2px 10px gray;
}

.btn:hover{
    cursor: pointer;
    transform: scale(1.040);
    transition: ease-in-out 0.3s;
}

